import React from 'react';
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
} from 'chart.js';
import { Chart } from 'react-chartjs-2';
// import faker from 'faker';

ChartJS.register(
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip
);

const options = {
  responsive: true,
  maintainAspectRatio: false,
  plugins: {
    legend: {
      position: 'top',
    },
    // title: {
    //   display: true,
    //   text: 'Chart.js Bar Chart',
    // },
  },
  layout: {
    padding: 20
  }
};


export default function ChartBilling({labelValue,labels}) {

  const data = {
    labels,
    datasets: [
      {
        type: 'line',
        label: 'Saldo',
        borderColor: 'rgb(255, 255, 255)',
        borderWidth: 2,
        fill: 2,
        cubicInterpolationMode: 'monotone',
        data: labelValue.map((e) => e.total),
      },
      {
        type: 'bar',
        label: 'Ingresos',
        backgroundColor: 'rgb(53, 162, 235)',//'rgb(75, 192, 192)',
        data: labelValue.map((e) => e.ingreso),
        borderColor: 'white',
        borderWidth: 2,
        stack: 'Stack 0',
        borderRadius: 5,
      },
      {
        type: 'bar',
        label: 'Egresos',
        backgroundColor: '#ff358888',
        data: labelValue.map((e) => e.egreso),
        stack: 'Stack 0',
        borderRadius: 5,
      },
    ],
  };
  return <Chart type='bar' data={data} options={options} />;
}