
import "./billing.scss";
import Grid from '@material-ui/core/Grid';
import BarBilling from "../../components/chartjs/BarBilling";
import DoughnutBilling from "../../components/chartjs/DoughnutBilling";
const randomInteger = ({ min, max }) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}
const labels = [
  'January', 'February', 'March', 'April', 'May', 'June', 'July', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre',
  'January', 'February', 'March', 'April', 'May', 'June', 'July', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'
];

export default function Billing(params) {

  let total = 0;
  let ingresos = 0;
  let egresos = 0;
  const labelValue = labels.map(() => {
    let ingreso = randomInteger({ min: 0, max: 1000 });
    let egreso = randomInteger({ min: -total, max: 0 });
    ingresos += ingreso;
    egresos += egreso;
    total += ingreso + egreso;
    return { ingreso, total, egreso }
  });

  return <div className="billing">
    <div className="top">
      <h1>Billing</h1>
      <span>Welcome back, Lucy! We've missed you. </span>
    </div>
    <div className="body">
      <Grid container >
        <Grid item xs={12} sm={12} md={4}>
          <Grid container >
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Ingresos</span>
                <h1>{ingresos}</h1>
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Retiros</span>
                <h1>{egresos}</h1>
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Saldo Actual</span>
                <h1>{total}</h1>
              </div>
            </Grid>
            <Grid item xs={12} sm={6}>
              <div className="section middle">
                <span>Follows</span>
                <h1>{randomInteger({ min: 0, max: 10000 })}</h1>
              </div>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <div className="section">
            <span>Billings</span>
            {/* <BarBilling labelValue={labelValue} labels={labels} /> */}
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={8}>
          <div className="section">
            <span>Map</span>
          </div>
        </Grid>
        <Grid item xs={12} sm={12} md={4}>
          <div className="section">
            <span>Source / Medium</span>
            <DoughnutBilling />
          </div>
        </Grid>
      </Grid>
    </div>
  </div>
}